}

/* 푸터 공통 */
#footer.type02{
  height: 83px;
  background: #fff;
  width: 100%;
  z-index: 100;
  .inner{max-width: 1200px;@include rem(padding, 26px 0 20px); height: 100%;margin: 0 auto;}
  .footer-wrap{display: flex;justify-content: space-between;}
  .footer-column{
      display: flex;
      align-self: center;
      .footer-copyright{@include rem(margin-left, 10px);@include rem(font-size, 14px);color: #999;word-break: break-all;word-wrap: break-word;}
