/* footer 공통 */
.f-fixed{
  .login-content-wrap{
      @include rem(margin-bottom,143px);
  }
  footer.type02{
      position: fixed;
      bottom: 0;
      left: 0;
  }
}

.pgheader_wrap{@include rem(height,60px);background:#fff;position:fixed;top:0;left:0;width:100%;z-index:100;box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);}
.pgheader_in{position:relative;}
.pgheader_wrap.type02{
  position: relative;
	@include rem(height,100px);
	box-shadow: none;
	.inner{max-width: 1200px;height: 100%;margin: 0 auto;@include rem(padding,29px 0);}
  h1{
    a{
      img{max-width: 100%;}
    }
