/* form 공통 */
.form-group{
  + .form-group{@include rem(margin-top,32px);}
}
.input-txt{
  border: none;
  label{@include rem(font-size,14px);color: #606266;}
  .input-box{
    display: flex;
    align-items: flex-end;
    .btn-lv01{
      @include rem(min-width,85px);
      @include rem(height,40px);
      @include rem(margin-left,8px);
      @include rem(padding,9px 5px);
      @include rem(border-radius,4px);
      background: #e1e3e8;
      text-align: center;
      .txt{@include rem(font-size,14px);color: #606266;}
    }
  }
  input{
    @include rem(height,40px);
    @include rem(margin-top,8px);
    @include rem(padding-right,6px);
    @include rem(font-size,14px);
    border: 1px solid #e4e7ed;
    @include rem(border-radius,4px);
    &::placeholder{color: #c0c4cc;@include rem(font-size,14px);}
    &.mt-0{margin-top: 0}
    &.h-56{@include rem(height, 56px);}
    &.h-28{@include rem(height, 28px);}
    &:disabled{
      background: #f5f7fa;
    }
    &.align-r{text-align: right;}
  }
  &:not(:first-child){@include rem(margin-top,16px);}
  &.form-success{
    input{border-color:#28a745 !important;}
    .message{color: #28a745 !important;}
  }
  &.form-error{
    input{border-color: #dc3545 !important;}
    .message{color: #dc3545 !important;}
  }
}
.selectbox{  
  &.form-success{
    select{border-color:#28a745 !important;}
    .message{color: #28a745 !important;}
  }
  &.form-error{
    select{border-color: #dc3545 !important;}
    .message{color: #dc3545 !important;}
  }
  &:disabled{
    background: #f5f7fa;
  }
}
.el-date-editor.el-input{
  &.form-success{
    .el-input__inner{border-color:#28a745 !important;}
    .message{color: #28a745 !important;}
  }
  &.form-error{
    .el-input__inner{border-color: #dc3545 !important;}
    .message{color: #dc3545 !important;}
  }
}
.form-tit{
  @include rem(padding-bottom, 16px);
  border-bottom: 1px solid #e4e7ed;
  .tit{@include rem(font-size,16px);font-weight: 500;color: #303133;}
}
.input-radio{
  .selectbox{@include rem(margin-top,15px);}
  &.mt-19{
    @include rem(margin-top,19px);
  }
}
.input-textarea{
  &.form-success{
    textarea{border-color:#28a745 !important;}
    .message{color: #28a745;}
  }
  &.form-error{
    textarea{border-color: #dc3545 !important;}
    .message{color: #dc3545;}
  }
}
.input-notice{
  .point-txt{
    display: block;
    @include rem(margin-top,16px);
    @include rem(padding-left,10px);
    @include rem(font-size,12px);
    @include rem(text-indent,-10px);
    color: #2883c5;
    line-height: 1.5
  }
}

/* 버튼 - 개인으로 사용시 */
button,a{
  // @include rem(min-width,80px);
  // @include rem(padding,10px 5px);
  text-align: center;
  @include rem(border-radius,4px);
  .txt{@include rem(font-size,14px);color: #606266;text-align: center;}
  &.wid-40{@include rem(min-width,40px);}
  &.wid-56{@include rem(min-width,56px);}
  &.wid-80{@include rem(min-width,80px);}
  &.wid-89{@include rem(min-width,89px);}
  &.wid-79{@include rem(min-width,79px);}
  &.wid-66{@include rem(min-width,66px);}
  &.wid-109{@include rem(min-width,109px);}
  &.hig-28{@include rem(height,28px);}
  &:hover{
    border-color: #aeb2ba !important;
  }
  &:disabled{
    border-color: #e4e7ed !important;
    background: #f9f9f9 !important;
    .txt{color: #d3d6dd !important;}
  }
  &.color-b{
    .txt{color: #0778ca;}
  }
  &.color-blue{
    background: #0778ca !important;
    border-color: #0778ca !important;
    .txt{color: #fff !important;}
    &:hover{
      background: #0b5eab !important;
      border-color: #0b5eab !important;
    }
    &:disabled{
      background: #cccccc !important;
      border-color: #cccccc !important;
      .txt{color: #fff !important;}
    }
  }
  &.color-blue02{
    background: #2883c5;
    .txt{color: #fff;}
  }
  &.color-blue03{
    background: #283356;
    .txt{color: #fff;}
  }
  &.color-red{
    background:#dc3545;
    border-color: #dc3545 !important;
    .txt{color: #fff;}
  }
  &.color-red02{
    border-color: #dc3545 !important;
    background: #fff6f8 !important;
    .txt{color: #dc3545 !important;}
    &:hover{
      border-color: #ba1626 !important;
      background: #ffd6db !important;
      .txt{color: #ba1626 !important;}
    }
  }
  &.color-orange{
    background: #dd682d;
    .txt{color: #fff !important;}
    &:hover{background: #c65319;}
    &:disabled{
      background: #cccccc !important;
      border-color: #cccccc !important;
      .txt{color: #fff !important;}
    }
  }
  &.color-white{
    background: #fff;
    .txt{color: #646266;}
  }
  &.border-blue{
    border: 1px solid #0778ca !important;
    .txt{color: #0778ca;}
    &:hover{
      border-color: #0b5eab !important;
      .txt{color: #0b5eab;}
    }
  }
  &.border-red{
    border:1px solid #dc3545 !important;
    background: #fff6f8;
    .txt{color: #dc3545;}
  }
  &.wid-full{
    width: 100%;
  }
  &.ml-8{@include rem(margin-left,8px);}
}
/* 버튼 - 그룹으로 사용시 */
.btn-group{
  @include rem(margin-top,24px);
  text-align: center;
  font-size: 0;
  button{
    display: inline-block;
    @include rem(min-width, 120px);
    @include rem(padding,8px 0 7px);
    @include rem(border-radius,4px);
    border: 1px solid #e4e7ed;
    text-align: center;
    .txt{@include rem(font-size,14px);text-align: center;}
    &.btn-confirm{background: #0778ca;border-color: #0778ca;}
    &.btn-close{
      .txt{color: #606266;}
    }
    + button{@include rem(margin-left,16px);}
  }
  a{
    @include rem(min-width,66px);
    width: 100%;
    @include rem(padding,9px 5px);
    border: 1px solid #e4e7ed;
    background: #fff;
    text-align: center;
    +a{@include rem(margin-left,16px);}
    .txt{@include rem(font-size,14px);color: #606266;text-align: center}
  }
  button,a{
    &.wid-58{@include rem(min-width,58px);}
    &.wid-63{@include rem(min-width,63px);}
    &.wid-66{@include rem(min-width,66px);}
    &.wid-77{@include rem(min-width,77px);}
    &.wid-79{@include rem(min-width,79px);}
    &.wid-80{@include rem(min-width,80px);}
    &.wid-89{@include rem(min-width,89px);}
    &.wid-92{@include rem(min-width,92px);}
    &.hig-28{
      @include rem(height,28px);
      @include rem(padding,4px !important);
      .txt{@include rem(font-size,12px);}
    }
    &.inline{
      position: relative;
      display: inline-block;
      min-width: auto;
      margin: 0;
      padding: 0;
      background: none;
      border: none;
      .txt{font-size: 18px;color: #606060}
      &::after{
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        width: 1px;
        height: 21px;
        background-color: #606060;
      }
      + .inline{margin: 0;}
      &:not(:last-child){
        @include rem(margin-right,10px);
        @include rem(padding-right,10px);
      }
    }
  }
  &.right{
    @include rem(margin,0 16px);
    text-align: right;
  }
  &.col-1{
    button{
      width: 100%;
      min-width: auto;
    }
  }
  &.h-56{
    button{@include rem(height,56px);}
  }
  &.flex{
    display: flex;
    justify-content: space-between;
    .write-btn-area{display: flex;}
    a,button{
      @include rem(max-width,66px);
      width: 100%;
      @include rem(padding,9px 5px);
      border: 1px solid #e4e7ed;
      @include rem(border-radius,4px);
    }
  }
  &.type-r{
    position: absolute;
    @include rem(bottom,24px);
    @include rem(right,24px);
  }
  &.type-tr{
    position: absolute;
    @include rem(top,24px);
    @include rem(right,24px);
    margin: 0;
  }
}

.notice-area{ 
  @include rem(margin-top,24px);
  .notice-txt{@include rem(font-size,14px);color: #000;}
}

